				index: 0,
				currentState: '在职'
			}
		},
		methods: {
			//下拉框
			bindPickerChange: function(e) {		//改变的事件名
				//console.log('picker发送选择改变，携带值为', e.target.value)   用于输出改变索引值
				this.index = e.target.value			//将数组改变索引赋给定义的index变量
				this.currentState=this.array[this.index]		//将array【改变索引】的值赋给定义的jg变量
				console.log("目前状态为：",this.currentState)		//输出获取的籍贯值，例如：中国
			},
		}
	}
</script>

